<script>
  //Back - End 
  import axios from 'axios'
  import endPoint from '../../config-endpoint/endpoint.js'
  import EventBus from '../components/EventBus'
  import io from 'socket.io-client';
  import * as moment from 'moment';
  import 'moment/locale/es';
  moment.locale('es');
  export default {
    data() {
      return {
        socket : io(endPoint.endpointTarget),
        activeNotifications: false,
        showMenu: false,
        searchQuery: '',
        nombre: localStorage.nombre + ' ' + localStorage.apellido,
        imgUser: endPoint.imgEndpoint + localStorage.imageUser,
        haveImage: localStorage.imageUser,
        imgEndpoint: endPoint.imgEndpoint,
        notifications: []
      };
    },
    created() {
    },
    methods: {
      toggleSidebar() {
        this.$sidebar.displaySidebar(!this.$sidebar.showSidebar);
      },
      hideSidebar() {
        this.$sidebar.displaySidebar(false);
        
      },
      toggleMenu() {
        this.showMenu = !this.showMenu;
        console.log('cerro')
      },
      getNotifications(){
        console.log('hpla')
        this.activeNotifications = false
        axios.get(endPoint.endpointTarget+'/notifications/onlyFive') 
        .then(res => {
          this.notifications = res.data
        })
      },
      momentTime(value) {
        const dateNoti = new Date(value)
        return moment(dateNoti, "YYYYMMDD").fromNow();
      }

    },
    mounted() {
      EventBus.$on('dataChange', status => {
        console.log(status)
        this.nombre = status.nombre + ' ' + status.apellido
        if (status.image != "") {
          this.imgUser = endPoint.imgEndpoint + status.image
        }
        console.log(this.imgUser)
      })
      this.socket.on('notify', (data) => {
        this.notifications.push(data)
        this.activeNotifications = true
        console.log(this.activeNotifications )
      });
    }
  };
</script>
